.custom-select {
  position: relative;
  width: 300px;
  padding: 14px 0 28px;
  margin: 0 0 44px 0;

  @media screen and (max-width: 767px) {
    padding: 4px 0 14px;
    width: 100%;
  }

  select {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }

  &.is-disabled {
    pointer-events: none;

    .custom-select__list {
      display: none;
    }
  }

  &.open-up {
    .custom-select__list {
      bottom: auto;
      top: 0;
      transform: translateY(calc(-100% + 10px));
    }
  }

  &.is-open {
    .custom-select__list {
      visibility: visible;
      opacity: 1;
    }

    .custom-select__icon {
      transform: rotate(180deg);
    }

    .custom-select__label {
      opacity: 0;
    }
  }

  &.not-empty {
    .custom-select__text {
      opacity: 1;
    }

    .custom-select__label {
      opacity: 0;
    }

    .custom-select__placeholder {
      opacity: 0;
    }
  }

  &.is-invalid {
    .custom-select__error {
      opacity: 1;
    }

    .custom-select__button {
      border: 2px solid $color-torch-red;

      &:focus,
      &:hover {
        border: 2px solid $color-wild-watermelon;
      }
    }
  }

  &.is-valid {
    .custom-select__button {
      border: 2px solid $color-green;

      &:focus,
      &:hover {
        border: 2px solid $color-la-palma;
      }
    }
  }

  &--placeholder {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    &.is-open .custom-select__label {
      opacity: 1;
    }

    &.not-empty .custom-select__label {
      opacity: 1;
    }
  }

  &.slide-up.not-empty,
  &.slide-up.is-open {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    .custom-select__text {
      opacity: 1;
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &.is-invalid {
      color: $color-torch-red;
    }

    &.is-valid {
      color: $color-green;
    }
  }
}

.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity  0.3s ease;
  pointer-events: none;

  @media screen and (max-width: 767px) {
    transform: translateY(calc(-50% - 5px));
  }
}

.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;

  @media screen and (max-width: 767px) {
    transform: translateY(calc(-50% - 5px));
  }
}

.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;

  &:hover {
    border: 2px solid rgba(0, 0, 0, 0.5);
  }

  &:focus {
    border: 2px solid rgba(0, 0, 0, 0.8);
  }

  &:disabled {
    pointer-events: none;
    opacity: 0.3;
  }
}

.custom-select__list {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: $color-default-white;
  border: 2px solid $color-default-white;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}

.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &[aria-selected="true"] {
    background-color: $color-alice-blue-ligth;

    @media (hover: hover) {
      &:hover:not(.focus-visible) {
        background-color: $color-alice-blue-ligth;
      }
    }

    &.focus-visible:focus {
      background-color: $color-alice-blue-ligth;
    }
  }

  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      background-color: $color-alice-blue-ligth;
    }
  }

  &.focus-visible:focus {
    background-color: $color-alice-blue-ligth;
  }
}

.custom-select__error {
  position: absolute;
  bottom: -15px;
  left: 0;
  color: $color-torch-red;
  opacity: 0;
  transition: opacity 0.1s ease;
}

.custom-select__text {
  font-size: 20px;
  line-height: 24px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}

.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: $color-default-black;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
