@media (max-width: ($vp-1920)) {
  .form {
    padding-top: 165px;
    padding-bottom: 250px;
    background: linear-gradient(319deg, $color-deep-sky-blue 66.3%, $color-cerulean 66%);
    margin-right: 10px;
  }

  .form__box {
    position: relative;
    max-width: 585px;
    margin-left: auto;
    padding-left: 5px;
    color: $color-default-white;

    &::before {
      position: absolute;
      content: "";
      width: 512px;
      height: 285px;
      top: 19%;
      left: -107%;

      @include graphic-retina("../img/paper-airplane", "png");
      background-size: 100%;
    }
  }

  .form h2 {
    font-family: $ff-roboto-condensed;
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;
    margin: 0;
    margin-bottom: 11px;
    margin-left: -5px;
  }

  .form p {
    margin: 0;
    margin-bottom: -4px;
  }

  .form__inner,
  .form__wrap {
    display: flex;
    gap: 30px;
    align-items: flex-end;
    margin-top: 37px;
  }

  .form__field {
    display: inline-block;
    flex: 1 1;
    width: 100%;
  }

  .form__field input,
  .form__comment textarea {
    padding: 15px 0 15px 15px;
    background: $color-pattens-blue;
    border: none;
    border-radius: 3px;
  }

  .form__field input {
    display: block;
    width: 100%;

    &::placeholder {
      color: $color-default-white;
    }

    &:not(:disabled):hover,
    &:not(:disabled):focus {
      outline: 1px solid $color-black-pearl;
    }

    &:active::placeholder {
      color: $color-default-white;
    }
  }

  .form__comment {
    margin-top: 20px;
  }

  .form__comment textarea {
    width: 100%;
    padding-top: 27px;
    padding-left: 14px;
    height: 155px;

    &::placeholder {
      color: $color-default-white;
    }

    &:not(:disabled):hover,
    &:not(:disabled):focus {
      outline: 1px solid $color-black-pearl;
    }

    &:active::placeholder {
      color: $color-default-white;
    }
  }

  .form__button {
    font-family: $ff-roboto-condensed;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $color-default-white;
    padding: 13px 70px;
    background: $color-black-pearl;
    border: none;
    border-radius: 3px;
    flex-shrink: 0;

    @include hover-focus {
      background: $color-alice-blue;
      outline: 2px solid $color-black-pearl;
      color: $color-black-pearl;
    }

    @include active {
      background: $color-alice-blue;
      outline: 2px solid $color-black-pearl;
      color: $color-black-pearl;
    }
  }

  .form__checkbox {
    position: relative;
    padding-left: 45px;
    margin-bottom: -4px;
  }

  .form__checkbox label {
    position: relative;
    display: inline-block;
    font-size: 14px;
    line-height: 16px;
    color: $color-default-white;
    opacity: 0.8;

    @include hover-focus {
      color: $color-black-pearl;
    }
  }

  .form__checkbox-visible::before {
    position: absolute;
    content: "";
    left: 19px;
    top: 8px;
    width: 15px;
    height: 15px;
    border: 1px solid $color-default-white;
    border-radius: 2px;
  }

  .form__checkbox input[type="checkbox"]:checked + .form__checkbox-visible::after {
    position: absolute;
    content: "";
    width: 7px;
    height: 7px;
    top: 11px;
    left: 22px;
    background-image: url("../../img/svg/check-mark.svg");
  }

  .form__checkbox-visible--modal::before {
    top: 4px;
    left: 9px;
  }

  .form__checkbox input:hover + .form__checkbox-visible::before,
  .form__checkbox input:focus + .form__checkbox-visible::before {
    border: 1px solid $color-black-pearl;
  }

  .form__inner--modal {
    flex-direction: column;
    margin-top: 39px;
    gap: 23px;
  }

  .form__comment--modal {
    margin-top: 22px;
  }

  .form__wrap--modal {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
    margin-top: 25px;
  }

  .form__checkbox--modal {
    padding-left: 34px;
  }

  .form__checkbox--modal input[type="checkbox"]:checked + .form__checkbox-visible--modal::after {
    top: 7px;
    left: 12px;
  }
}

@media (max-width: ($vp-1920 - 1px)) {
  .form {
    margin-right: 0;
    padding-bottom: 252px;
    background: linear-gradient(319deg, $color-deep-sky-blue 69.5%, $color-cerulean 69.5%);
  }

  .form__box {
    padding-left: 3px;

    &::before {
      height: 287px;
      width: 514px;
    }
  }

  .form__inner {
    margin-top: 35px;
  }

  .form h2 {
    margin-bottom: 11px;
  }

  .form p {
    margin-bottom: 0;
  }

  .form textarea {
    padding-left: 16px;
  }
}

@media (max-width: ($vp-1024 - 1px)) {
  .form {
    background: $color-deep-sky-blue;
    padding-top: 44px;
    padding-bottom: 125px;
  }

  .form__box {
    max-width: 100%;
    margin: 0;
    padding-left: 0;

    &::before {
      content: none;
    }
  }

  .form h2 {
    margin-left: 0;
    margin-bottom: 5px;
  }

  .form__inner,
  .form__wrap {
    margin-top: 25px;
  }

  .form textarea {
    padding-top: 34px;
    padding-left: 14px;
  }

  .form__checkbox {
    margin-bottom: 0;
  }

  .form__button {
    margin-right: 34px;
    margin-top: 13px;
  }
}

@media (max-width: ($vp-768 - 1px)) {
  .form {
    padding-top: 47px;
    padding-bottom: 83px;
  }

  .form p {
    font-size: 14px;
    line-height: 134.4%;
    max-width: 200px;
    margin-bottom: 35px;
  }

  .form h2 {
    font-weight: 400;
    font-size: 30px;
    line-height: 140.62%;
    margin-bottom: 8px;
  }

  .form__inner,
  .form__wrap {
    flex-direction: column;
    gap: 20px;
    margin-top: 18px;
  }

  .form__field label {
    max-width: 100%;
  }

  .form__comment {
    margin: 0;
    margin-top: 23px;
  }

  .form textarea {
    padding-top: 24px;
    height: 180px;
  }

  .form__button {
    margin-right: 0;
    margin-bottom: 5px;
    width: 100%;
  }

  .form__checkbox {
    padding-left: 30px;
  }

  .form__checkbox-visible {
    &::before {
      left: 4px;
      top: 5px;
    }
  }

  .form__checkbox input[type="checkbox"]:checked + .form__checkbox-visible::after {
    left: 7px;
    top: 9px;
  }
}
